@media screen and (max-width: 1200px) {
   .container{
      padding: 0 1rem;
   }
   .header-top{
      position: relative;
      z-index: 99;
      max-width: 530px;
      margin-bottom: 1rem;
   }
   .header-img__prod{
      width: 170%;
      & img{
         width: 100%;
      }
   }
   .header-down,.header-top__price{margin-top: 0;}
   .header-top__sale{
      margin: 0 0 0 auto;
   }
   .header-img__prod:before{
      transform: translate(-5%, 1000%) rotate(-21deg);
   }











   .prod-galery-slider{
      max-width: 400px;
      height: 400px;
      margin: 0 auto;
   }






   .rew-wrapp{
      transform: translate(30%, 0);
   }
   .rew-block{
      max-width: 650px;
   }
   
   



   .screen{
      flex-wrap: wrap;
      justify-content: center;
   }
   .screen__item{
      margin: 0 .5rem 1rem;
   }
}


@media screen and (max-width: 991px) {
  .header-up{
     flex-direction: column-reverse;
     margin-bottom: 3rem;
  }
  .header-top{
     max-width: 700px;
     margin: 0 auto;
     &__logo{
        margin: 0 auto 1rem;
     }
     &__title{
        text-align: center;
     }
     &__subtitle{
      text-align: center;
      max-width: 100%;
      margin-bottom: 2rem;
   }
   &__sale{
      margin: 0 auto 1rem 20%;
      position: relative;
      z-index: 90;
   }
   &__price{
      margin: 0 auto 0 42%;
      &:before{
         width: 200px;
         height: 200px;
         transform: translate(-65%, -50%);
      }
   }
  }
  .header:before{
   transform: translate(-200%, -35%);
  }
  .header:after{
   transform: translate(160%, -35%);
  }



  .header-img{
     margin: 0 auto;
     &__prod{
        transform: translate(-40%,-10%);
     }
  }
  .header-down{
     margin: 0 auto;
     max-width: 700px;
     &__info{
        margin-bottom: 1rem;
     }
  }







  .info-top{
     margin-bottom: 2rem;
     &:before{
      transform: translate(-160%, 10%);
     }
     &:after{
      transform: translate(80%, -40%);
     }
  }

  .info-top-prod{
   & img{
      width: 170%;
   }
  }














   .spray-title{
      margin-bottom: 4rem;
   }
  .spray{
     flex-direction: column;
     align-items: center;
     margin-top: 0;
  }
  .spray-order{
     width: 100%;
     max-width: 600px;
  }
  .spray-order__info{
     padding-top: 0;
     margin-bottom: 3rem;
  }
  .spray-order__info:before{
   transform: translate(-60%, -50%);
  }











  .product-title{
     text-align: center;
  }
  .product-wrap,.product-order{
     flex-direction: column;
     align-items: center;
  }
  .product-galery{
     margin-bottom: 1.5rem;
  }
  .product-order{
     padding-top: 0;
  }
  .prod-form{
     margin-top: 0;
  }
  .product-order__price,.product-info{
     width: 60%;
     margin-bottom: 1rem;
  }










  .whay-wrap{
     flex-wrap: wrap;
     align-items: center;
  }
  .whay-block{
     max-width: 260px;
     align-items: center;
  }





  .how-wrap:before{
     top: 27%;
  }















  .rew-prod{
     position: relative;
     transform: translate(0, -25%);
     left: 0;  
     & img{
        margin: 0 auto;
     }
  }
  .reviews-content:before{
   transform: translate(-200%, -15%);
  }
  .reviews-content:after{
   transform: translate(60%, -15%);
  }
  .rew-title{
     max-width: 100%;
     margin: -30rem 0 2rem;
  }
  .rew-wrapp{
   transform:translate(10%, 0);
  }













  .footer__prod{
   transform: translate(-20%, 30%);
     & img{
        width: 150%;
     }
  }
}


@media screen and (max-width: 767px) {
   .header-content{
      padding-top: .5rem;
   }
   .header-top{
      &__logo{
         width: 180px;
         & img{
            width: 100%;
         }
      }
      &__title{
         line-height: 1;
         font-size: 28px;
      }
      &__subtitle{
         line-height: 1;
         font-size: 20px;
      }
      &__sale{
         margin-left: 15%;
      }
   }
   .header-img__circle{
      max-width: 400px;
      height: 400px;
      padding-bottom: 0;
   }
   .header-img__prod{
      width: 140%;
      transform: translate(-40%, -12%);
   }
   .header-img__prod:before{
      display: none;
   }

   .header:before{
      transform: translate(-180%, -45%);
     }
     .header:after{
      transform: translate(135%, -10%);
     }











     .info-top{
        flex-wrap: wrap;
        justify-content: center;
        &:before{
         transform: translate(-130%, 10%);
        }
        &:after{
         transform: translate(60%, -80%);
        }
     }
     .info-top-img{
        width: 40%;
     }












     .spray-title{
        font-size: 30px;
     }
     














     .galery{
        &:after{
         transform: translate(70%, -50%);
        }
        &:before{
         transform: translate(-70%, 60%);
         width: 210px;
         height: 160px;
         background-size: 100%;
        }
        &-wrap:nth-child(1){
           width: 100%;
        }
        &-wrap:nth-child(2){
         width: 100%;
      }
      &-wrap:nth-child(3){
         flex-wrap: wrap;
         & .galery__item{
            width: 48%;
         }
      }
     }
     .galery-content{
        padding: 3rem 0 5rem;
     }














     .product-content:before{
      z-index: 99;
     }
     .product{
        z-index: 100;
     }








     .product-galery{width: 500px;}
     .product-info{
        width: 100%;
     }
     .product-galery__wrap{
      margin: 0 auto;
     }
     .product-info-list,.prod-sizes{
        max-width: 600px;
        margin: 0 auto 1rem;
     }
     .prod-sizes,.prod-color{
      max-width: 600px;
      margin: 0 auto 2rem;
   }
   .product-order__price{
      width: 85%;
      flex-wrap: wrap;
      justify-content: center;
   }











   .galery-content-1{
      padding-bottom: 0;
   }
   .galery-1{
      flex-wrap: wrap;
   }
   .galery-1-wrap{
      width: 100% !important;
   }
   .galery-1-wrap:nth-child(2){
      & .galery__item{
         width: 100%;
      }
   }




   .about-content:before{
      display: none;
   }
   .about{
      flex-direction: column;
      &__title{
         width: 100%;
         text-align: center;
         margin: 0 auto 1.5rem;
         font-size: 30px;
      }
      &__text{
         font-size: 18px;
         width: 100%;
         margin: 0 auto 1.5rem;
         padding: 0;
      }
   }







   .galery-2{
      flex-direction: column;
   }
   .galery-2-wrap{
      width: 100% !important;
   }
   .galery-2-wrap:nth-child(1){
      & .galery__item{
         width: 100%;
      }
   }












   .how-wrap{
      flex-wrap: wrap;
   }
   .how-block{
      margin: 0 auto;
   }
   .how-wrap:before,.how-wrap__item:not(:first-child):before{
      display: none;
   }
   .whay-how{
      padding: 1rem 0;
   }
   .whay-title,.how-title{
      font-size: 30px;
      text-align: center;
      margin-bottom: 1.8rem;
   }
   .whay-wrap__item ,.how-wrap__item{
      width: 48%;
      &:not(:last-child){
         margin-bottom: 1.5rem;
      }
   }
   .how-wrap__item{
      margin-bottom: 2.3rem;
   }
   .whay-block__icon{
      min-width: 100px;
      height: 100px;
      margin-right: 1.5rem;
   }
   .how-block__icon{
      width: 100px;
      height: 100px;
      margin: 0 auto 1.5rem;
   }
   .whay-icon,.how-icon{
      width: 50px;
      height: 50px;
      background-size: contain !important;
   }






   .rew-title{
      font-size: 30px;
      margin-top: -20rem;
   }
   .rew-wrapp{
      transform: none;
      &:before{
         content: '';
         position: absolute;
         right: -100px;
         top: 0;
         background-color: #fff;
         height: 100%;
         width: 300px;
      }
   }
   .rew-block{
      max-width: 550px;
      min-height: 400px;
   }
   .rew-block__text{
      width: 100%;
   }
   .rew-prod{
      & img{
         width: 120%;
         margin-left: -10%;
      }
   }
   .reviews-content:before{
      transform: translate(-150%, -15%)
   }
   .reviews-content:after{
      transform: translate(30%, -15%)
   }
   .screen{
      width: 100%;
   }
   .screen__item{
      margin: 0 0 1rem;
      width: 50%;
      & img{
         width: 95%;
      }
   }
   .rew-wrapp .slick-dots{
      text-align: left;
      padding-left: 2rem;
   }
   .rew-btn:after{
      transform: translate(10%, -30%);
   }












   .footer__title{
      width: 100%;
      text-align: center;
      margin: 0 auto 2rem;
      font-size: 30px;
      position: relative;
   }
   .footer__sale{
      text-align: center;
      margin-bottom: 2rem;
      position: relative;
      z-index: 6;
   }
   .footer__price{
      max-width: 270px;
      margin: 0 auto 6rem;
      display: flex;
      flex-direction: row-reverse;
      align-items: flex-end;
      transform: none;
      & .header-pr-old{
         font-size: 24px;
      }
      & .header-pr-new{
         font-size: 55px;
         margin-bottom: -5px;
      }
      &:before{
         transform: translate(-50%, -20%);
      }
   }
   .footer__prod{
      position: relative;
      left: 0;
      transform: none;
      width: 100%;
      margin-top: -5rem;
      & img{
         width: 100%;
      }
   }
   .footer__btn{
      margin: 0 auto 3rem;
      position: relative;
      z-index: 9;
   }
   .footer__copy{
      width: 60%;
      text-align: right;
      margin: 0 0 0 auto;
   }
   .footer:before{
      transform: translate(-30%, 40%);
   }
}




@media screen and (max-width: 480px) {
   .header-top{
      margin-bottom: 1rem;
      &__logo{
         width: 130px;
         & img{
            width: 100%;
         }
      }
      &__title{
         line-height: 1;
         font-size: 28px;
         max-width: 280px;
      }
      &__subtitle{
         display: none;
      }
      &__sale{
         margin-left: 1rem;
      }
   }
   .header-top__price{
      width: 200px;
      margin: -.5rem auto 0 35%;
      &:before{
         transform: translate(-48%, -40%);
         z-index: -1;
      }
   }



   .header-pr-old{
      font-size: 24px;
   }
   .header-pr-new{
      font-size: 48px;
   }


   .header-img{
      z-index: 100;
   }
   .header-img__circle{
      margin-top: 5px;
      max-width: 250px;
      height: 250px;
      padding-bottom: 0;
      position: relative;
   }
   .header-img__prod{
      width: 75%;
      transform: translate(-75%, -17%);
   }
   .header-img:before{
      transform: translate(-210%, -140%);
   }
   .header-img:after{
      transform: translate(-600%, -450%);
   }
   .header-img__circle:after{
      transform: translate(75%, -70%);
   }
   .header-img__prod:after{
      transform: translate(200%, 250%);
   }
   .header-pr-old:before{
      width: 86%;
      left: 7%;
   }
   .header-img__circle:before{
      transform: translate(-70%, -50%);
   }

   .header:before{
      width: 354px;
      height: 494px;
      transform: translate(-133%, -30%);
      background: url(../img/content/header-mob-flover.png)center center no-repeat;
     }
     .header:after{
      transform: translate(50%, 0%);}
      .head-info{
         width: 100% !important;
      }
      .header-down__btn{
         margin:0 auto 1.9rem;
      }
      .head-info__text{
         font-size: 18px;
      }














      .info-top{
         flex-wrap: wrap;
         justify-content: center;
         &:before{
          transform: translate(-110%, -260%);
         }
         &:after{
          transform: translate(20%, 30%);
         }
      }
      .info-top-img{
         width: 80%;
         margin: 0 auto 1rem !important;
      }
      .info-top-prod{
         width: 85%;
         margin-top: -12rem;
      }
      .info-text{
         margin-top: -3rem;
         font-size: 18px;
         color: #4a2a1b;
         text-align: left;
      }











      .spray-info-title{
         width: 290px !important;
         text-align: center;
         transform: translate(-30%,0);
      & br{
         display: none;
      }
      }
      .spray-order__info:before{
         transform: translate(-45%, -55%)
      }
      .spray-order__info{
         width: 68%;
         margin-bottom: 0;
      }
      .spray-order__img{
         width: 93px;
         & img{
            width: 250%;
            transform: translate(-50%, -60%);
         }
      }
      .spray-order__info:after{
         transform: translate(-110%, -40%)
      }









      .product-galery{
         width: 100%;
      }
      .prod-galery-slider{
         height: auto;
      }
      .product-title{
         font-size: 30px;
         & span{
            display: block;
         }
      }
      .slider-btn-r{
         right: 0;
      }
      .slider-btn-l{
         left: 0;
      }
      .product-info-list__item{
         display: block;
         font-size: 18px;
      }
      .prod-sizes,.prod-color{
         flex-wrap: wrap;
         &__title{
            margin-bottom: 1rem;
         }
      }
      .product-order__form{
         width: 100%;
      }
      .prod-form{
         margin: 0;
      }
      .my-btn{
         width: 280px;
      }
      .product-order__price{
         width: 100%;
         margin: 0 auto 3rem;
      }
















      .whay-wrap__item{
         width: 100%;
      }
      .whay-block{
         max-width: 100%;
      }














      .rew-prod{
         width: 100%;
         & img{width: 500px;
         margin-left: -135px}
      }
      .reviews-content:before{
         width: 200px;
         background-size: 100%;
         transform: translate(-140%, -30%)
      }
      .reviews-content:after{
         width: 270px;
         background-size: 100%;
         transform: translate(25%, -20%)rotate(-20deg)
      }
      .rew-btn:after{
         width: 150px;
         background-size: 100%;
         transform: translate(-45%, -45%);
      }
      .rew-btn{
         margin-bottom: 4rem;
      }










       .footer__price{
         &:before{
            width: 370px;
            background-size: 100%;
            transform: translate(-50%, -40%);
         }
      }
      .footer__prod{
         & img{
            width: 116%;
            margin-left: -8%;
         }
      }
      .footer:before{
         transform: translate(-40%, 65%);
      }
      .footer__copy{
         font-size: 14px;
         line-height: 1.2;
      }
      .submit-btn.my-btn{
         font-size: 20px;
      }
}