// =============================================================

html{
   font-size: calc( (100vw - 320px)/(1920 - 320) * (16 - 14) + 14px);
}

html,body{
   width: 100%;
   overflow-x: hidden;
   margin: 0;
   padding: 0;
}
 
body{
   font-family: 'Futura', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif !important;
   font-weight: 400;
   line-height: 1;
}

*{
   outline: none;
   box-sizing: border-box;
}

.container{
   max-width: 1140px;
   min-width: 320px;
   margin: 0 auto;
}


// =======================================================================


body.modal-open {
   overflow-x: hidden;
   overflow-y: auto;
}

.modal {
   display: none;
   background: rgba(0, 0, 0, .30);
   z-index: 999;
   padding: 30px 0;

   position: fixed;
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;

   overflow-x: hidden;
   overflow-y: auto;
}
.modal-content{
   -webkit-transition: all .4s ease-in-out;
   transition: all .4s ease-in-out;
}
.modal .modal-dialog {
   position: relative;
   max-width: 1100px;
   // border-radius: 6px;
   // -webkit-box-shadow: 0 3px 9px rgba(0, 0, 0, .5);
   //         box-shadow: 0 3px 9px rgba(0, 0, 0, .5);

   // background: #fff;
   margin: 0 auto;
}

.my-modal-content{
  max-width: 600px;
  width: 90%;
  margin: 0 auto;
background-color: #43b9ab;
  padding: 2rem 1rem;
  border-radius: 6px;
  position: relative;
  -webkit-box-shadow: 0 10px 15px #666;
          box-shadow: 0 10px 15px #666;
}
.modal-btn-close{
   position: absolute;
   right: 0;top: 0;
}
.my-modal-content__title{
   width: 100%;
   text-align: center;
   font-size: 1.8rem;
   font-weight: 600;
   color: #fff;
   margin-bottom: 1.5rem;
}
