// =======================================================================

$powder: #fed4c1;
$white: #fff;
$blue-green: #1ba68f;   
$braun: #7e5a4a;
$dark-braun: #4a2a1b;
$red: #ff4c4c;
$yellow: #ffc000;
$btn-shadow: 0 0 10px rgba(0,0,0,.4);
//========================================================================





.header-content{
   width: 100%;
   padding: 2.1rem 0 1rem;
   background: $powder;
   position: relative;
   overflow: hidden;
   &::before{
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      width: 75rem;
      height: 50rem;
      background: url(../img/content/header-bg.png)center center no-repeat;
      pointer-events: none;
      // transform: translate(-35%,0);
   }
}
.header{
   position: relative;
   &:before{
      content: '';
      position: absolute;
      left: 50%;
      top: 0;
      width: 340px;
      height: 493px;
      background: url(../img/content/header-flower.png)center center no-repeat;
      z-index: 100;
      transform: translate(-250%,-35%);
   }
   &:after{
      content: '';
      position: absolute;
      left: 50%;
      top: 0;
      width: 225px;
      height: 222px;
      background: url(../img/content/header-flower-1.png)center center no-repeat;
      z-index: 100;
      transform: translate(270%,-15%);
   }
}
.header-up{
   display: flex;
   justify-content: space-between;
   position: relative;
}
.header-img{
   width: 510px;
   position: relative;
   z-index: 99;
   &:before{
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width:   100px;
      height:  100px;
      border-radius: 50%;
      background-color: #42b8b3;
      transform: translate(-260%,-200%);
      z-index: -1;
   }
   &:after{
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width:   40px;
      height:  40px;
      border-radius: 50%;
      background-color: #feeae3;
      z-index: 999;
      transform: translate(-500%,-650%);
   }
   &__circle{
      max-width: 500px;
      height: 0;
      padding-bottom: 100%;
      border-radius: 50%;
      border: 2px solid $white;
      pointer-events: none;
      position: relative;
      &:before{
         content: '';
         position: absolute;
         left: 50%;
         top: 50%;
         width: 200px;
         height: 200px;
         border-radius: 50%;
         background-color: #feeae3;
         z-index: -1;
         transform: translate(-140%, 50%);
      }
      &:after{
         content: '';
         position: absolute;
         left: 50%;
         top: 50%;
         width:   100px;
         height:  100px;
         border-radius: 50%;
         background-color: #42b8b3;
         z-index: -1;
         transform: translate(160%, -150%);
      }
   }
   &__prod{
      position: absolute;
      left: 50%;
      top: 0;
      transform: translate(-45%,0);
      z-index: 2;
      pointer-events: none;
      &:before{
         content: 'количество товара ограничено';
         position: absolute;
         left: 50%;
         top: 50%;
         width: 300px;
         color: $red;
         font-size: 1.125rem;
         text-transform: lowercase;
         transform: translate(-5%,1150%) rotate(-21deg);
      }
      &:after{
         content: '';
         position: absolute;
         left: 50%;
         top: 50%;
         width: 40px;
         height: 40px;
         border-radius: 50%;
         background-color: #46b8b4;
         z-index: -1;
         transform: translate(-680%,550%);
      }
   }
}
.header-top{
   max-width: 630px;
   position: relative;
   &__logo{
      max-width: 200px;
      margin: 0 0 1rem auto;
      & img{
         width: 100%;
      }
   }
   &__title{
      width: 100%;
      font-size: 3rem;
      color: $blue-green;
      text-align: right;
      margin-bottom: 1.5rem;
   }
   &__subtitle{
      max-width: 540px;
      font-size: 1.5rem;
      color: $braun;
      text-align: right;
      margin: 0 0 4.6rem auto;
   }
   &__sale{
      max-width: 220px;
      font-size: 3rem;
      font-weight: 600;
      text-transform: uppercase;
      color: #42b7b3;
      white-space: nowrap;
      margin: 0 auto 0 50%;
   }
   &__price{
      max-width: 250px;
      margin: -2rem auto 3rem 20%;
      position: relative;
      &:before{
         content: '';
         position: absolute;
         left: 50%;
         top: 50%;
         width: 12.5rem;
         height: 12.5rem;
         border-radius: 50%;
         background-color: #feeae3;
         transform: translate(-75%,-50%);
      }
   }
}
.header-pr-old{
   position: relative;
   max-width: 110px;
   font-size: 1.875rem;
   color: #bb9d90;
   white-space: nowrap;
   position: relative;
   text-align: center;
   &:before{
      content: '';
      position: absolute;
      left: 0;
      top: 60%;
      width: 100%;
      height: 2px;
      background-color: #bb9d90;
   }
}
.header-pr-new{
   position: relative;
   font-size: 4.5rem;
   font-weight: 600;
   color: $red;
   text-align: right;
   white-space: nowrap;
   margin-top: -10px;
   list-style: -2px;
}





.header-down{
   position: relative;
   max-width: 470px;
   margin: -3.5rem 4% 0 auto;
   &__btn{
      max-width: 300px;
      margin: 0 0 5.3rem auto;
   }
   &__info{
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
   }
}

.header-link{
   max-width: 300px;
   height: 60px;
   display: flex !important;
   align-items: center;
   justify-content: center;
   font-size: 2.2rem;
   text-transform: lowercase;
   color: $white !important;
   background-color: $yellow !important;
   border: 1px solid $white;
   border-radius: 20px;
   margin-bottom: 10px;
   box-shadow: $btn-shadow;
   transition: all .4s ease-in-out;
   &:hover{
      background-color: $white !important;
      color: $yellow !important;
      box-shadow: 0 0 0;
      border-color: $yellow;
   }
}
.header-link-subtitle{
   text-align: center;
   color: $blue-green;
   text-transform: lowercase;
   font-size: 1.125rem;
   font-weight: 600;
}
.head-info:nth-child(1),.head-info:nth-child(2){
   width: 45%;
   margin-bottom: 3.1rem;
}
.head-info{
   display: flex;
   align-items: center;
}
.head-info__icon{
   min-width: 52px;
   height: 52px;
   margin-right: 12px;
}
.info-icon{
   width: 52px;
   height: 52px;
   display: block;
   &-1{
      background: url(../img/icons/header-icon-1.png)center center no-repeat;
      background-size: contain;
   }
   &-2{
      background: url(../img/icons/header-icon-2.png)center center no-repeat;
      background-size: contain;
   }
   &-3{
      background: url(../img/icons/header-icon-3.png)center center no-repeat;
      background-size: contain;
   }
}
.head-info__text{
   text-transform: uppercase;
   font-size: 1.25rem;
   color: $dark-braun;
}