.info-content {
   background: $powder;
   padding: 1rem 0 3.75rem;
   position: relative;
}

.info-top {
   width: 100%;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: justify;
   -ms-flex-pack: justify;
   justify-content: space-between;
   margin-bottom: 7.5rem;
   position: relative;

   &:before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width: 469px;
      height: 184px;
      background: url(../img/content/info-flower-1.png)center center no-repeat;
      z-index: 99;
      -webkit-transform: translate(-180%, 35%);
      transform: translate(-180%, 35%);
   }

   &:after {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width: 383px;
      height: 276px;
      background: url(../img/content/info-flower-2.png)center center no-repeat;
      z-index: 99;
      -webkit-transform: translate(120%, -10%);
      transform: translate(120%, -10%);
   }

   &-img {
      width: 250px;
      height: 300px;
      border: 1px solid $white;
      margin-right: 10px;
      -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, .3);
      box-shadow: 0 0 5px rgba(0, 0, 0, .3);

      &:first-child {
         margin-top: -2rem;
      }

      & img {
         width: 100%;
         height: 100%;
         -o-object-fit: cover;
         object-fit: cover;
      }
   }

   &-prod {
      width: 55%;
      min-height: 300px;
      position: relative;

      & img {
         position: absolute;
         left: 50%;
         top: 50%;
         -webkit-transform: translate(-50%, -50%);
         transform: translate(-50%, -50%);
      }
   }
}

.info-text {
   max-width: 1040px;
   margin: 0 auto;
   text-align: center;
   font-size: 1.125rem;
   color: $dark-braun;
   line-height: 1.2;
}









.spray-content {
   background: $powder;
   padding: 1.5rem 0;
   margin-top: -1px;
}

.spray-title {
   max-width: 48.8rem;
   margin: 0 auto 2.5rem;
   text-align: center;
   color: $blue-green;
   font-size: 2.25rem;
   font-weight: 600;
   text-transform: uppercase;
   line-height: 1.2;

   & span {
      color: $red;
      font-size: 3rem;
   }
}

.spray {
   margin-top: -8rem;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: justify;
   -ms-flex-pack: justify;
   justify-content: space-between;
   position: relative;
   z-index: 99;

   &-order {
      width: 43.86%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
      -ms-flex-pack: justify;
      justify-content: space-between;
      -ms-flex-wrap: wrap;
      flex-wrap: wrap;
      position: relative;

      &:last-child {
         margin-top: 4rem;
      }

      &__img {
         width: 20%;
         min-height: 280px;
         position: relative;

         & img {
            position: absolute;
            left: 50%;
            top: 50%;
            -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
         }
      }

      &__info {
         padding-top: 10rem;
         width: 78%;
         position: relative;

         &:before {
            content: '';
            position: absolute;
            left: 50%;
            top: 50%;
            width: 578px;
            height: 447px;
            background: url(../img/content/spray-bg.png)center center no-repeat;
            z-index: -1;
            -webkit-transform: translate(-60%, -43%);
            transform: translate(-60%, -43%);
         }

         &:after {
            content: '';
            position: absolute;
            left: 50%;
            top: 50%;
            width: 294px;
            height: 357px;
            background: url(../img/content/spray-bg-1.png)center center no-repeat;
            z-index: -2;
            -webkit-transform: translate(-130%, -20%);
            transform: translate(-130%, -20%);
         }
      }

      &__form {
         width: 100%;
      }
   }
}

.spray-info-title {
   width: 100%;
   font-size: 1.875rem;
   line-height: 1.1;
   color: $dark-braun;
   font-weight: 600;
   margin-bottom: 1.5rem;
}

.spray-info-text {
   padding: 0 1rem;
   color: #666;
   font-size: 1.125rem;
   line-height: 1.1;
   margin-bottom: 2rem;

   & p:not(:last-child) {
      margin-bottom: 1.5rem;
   }
}

.spray-info-price {
   max-width: 140px;
   margin: 0 auto 3.75rem 10%;

   &__old {
      font-size: 1.25rem;
      color: #666;
      width: 5rem;
      position: relative;
      text-align: center;
      white-space: nowrap;

      &:before {
         content: '';
         position: absolute;
         left: 0;
         top: 60%;
         width: 100%;
         height: 1px;
         background-color: #666;
         opacity: .6;
      }
   }

   &__new {
      font-size: 2.5rem;
      font-weight: 600;
      color: #ffa200;
      white-space: nowrap;

   }
}

.ord-red {
   color: $red;
   font-size: 1.25rem;
   line-height: 1.3;

   & span {
      font-size: 1.5rem;
      font-weight: 600;
   }
}







