.order-form {
   width: 100%;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: center;
   -ms-flex-pack: center;
   justify-content: center;
   -ms-flex-wrap: wrap;
   flex-wrap: wrap;
}

.form-group {
   padding: 0 5px;
}

.my-input,.rev-input {
   width: 230px;
   background-color: $white;
   color: #666;
   font-size: 16px !important;
   padding: 20px 18px 10px;
   border: none;
   outline: none;
   border-radius: 6px;
   -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, .3);
   box-shadow: 0 0 5px rgba(0, 0, 0, .3);
   -webkit-transition: all .3s ease-in-out;
   transition: all .3s ease-in-out;
   margin-bottom: 20px;

   &:focus {
      padding: 15px 18px;
      color: $dark-braun;
      -webkit-box-shadow: 0 0 0;
      box-shadow: 0 0 0;
   }
}


.my-btn {
   width: 300px;
   height: 60px;
   margin: 0 auto;
   text-align: center;
   text-transform: lowercase;
   letter-spacing: 2px;
   font-size: 2.25rem;
   border-radius: 20px;
   -webkit-box-shadow: $btn-shadow;
   box-shadow: $btn-shadow;
   -webkit-transition: all .3s ease-in-out;
   transition: all .3s ease-in-out
}

.spray-order__form .my-btn {
   color: $white;
   border: 1px solid $white;
   background-color: #1ca68f;

   &:hover {
      background-color: $white;
      color: #1ca68f;
      border-color: #1ca68f;
      -webkit-box-shadow: 0 0 0;
      box-shadow: 0 0 0;
   }
}










