.galery-content {
   background: $powder;
   padding: 8.8rem 0;
   margin-top: -1px;
   position: relative;

   &:before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width: 1023px;
      height: 1336px;
      background: url(../img/content/galery-bg-1.png)center center no-repeat;
      pointer-events: none;
      transform: translate(-105%, -85%);

   }

   &:after {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width: 1102px;
      height: 1376px;
      background: url(../img/content/galery-bg-2.png)center center no-repeat;
      pointer-events: none;
      transform: translate(-5%, -50%);
      z-index: 1;
   }
}

.galery {
   width: 100%;
   display: flex;
   justify-content: space-between;
   flex-wrap: wrap;
   position: relative;
   z-index: 99;

   &:before {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 264px;
      height: 227px;
      background: url(../img/content/galery-flower-1.png)center center no-repeat;
      z-index: 2;
      pointer-events: none;
      transform: translate(-90%, 0);
   }

   &:after {
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      width: 162px;
      height: 153px;
      background: url(../img/content/galery-flower-2.png)center center no-repeat;
      z-index: 2;
      pointer-events: none;
      transform: translate(50%, -50%);
   }
}

.galery-wrap {
   display: flex;
   width: 100%;

   &:nth-child(1) {
      width: 49%;
      flex-wrap: wrap;
      justify-content: space-between;

      & .galery__item {
         width: 48%;
      }
   }

   &:nth-child(2) {
      width: 49%;

      & .galery__item {
         width: 100%;
      }
   }

   &:nth-child(3) {
      justify-content: space-between;

      & .galery__item {
         width: 23.5%;
      }
   }
}

.galery__item {
   position: relative;
   overflow: hidden;
   margin-bottom: 4%;

   & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all .4s ease-in-out;
   }
}

.galery-cover {
   position: absolute;
   left: 0;
   top: 0;
   width: 100%;
   height: 100%;
   background-color: rgba(28, 166, 143, .7);
   display: flex;
   align-items: center;
   justify-content: center;
   opacity: 0;
   transition: all .4s ease-in-out;
}

.icon-plus {
   font-size: 6.25rem;
   color: $white;
   font-weight: 300;
   opacity: 0;
   transform: translate(-200px, -200px);
   transition: all .4s ease-in-out;
}

.galery__item {
   border: 1px solid #fff;
   &:hover {
      & img {
         transform: scale(1.2);
      }

      & .galery-cover {
         opacity: 1;
      }

      & .icon-plus {
         opacity: 1;
         transform: translate(0, 0)rotate(720deg);
      }
   }
}










.product-content {
   width: 100%;
   position: relative;
   background: url(../img/content/prod-bg-2.jpg)center top repeat-y;
   background-size: cover;
   padding-bottom: 3rem;

   &:before {
      content: '';
      position: absolute;
      left: 50%;
      top: 0;
      width: 100%;
      height: 214px;
      background: url(../img/content/prod-bg-1.png)center top no-repeat;
      background-size: cover;
      transform: translate(-50%, -50%);
   }
}

.product {
   position: relative;

   &:not(:last-child) {
      margin-bottom: 7.5rem;
   }
}

.product-title {
   font-size: 2.5rem;
   text-transform: uppercase;
   color: $white;
   margin-bottom: 2rem;

   & span {
      font-size: 1.5rem;
      color: #195f5c;
   }
}

.product-wrap {
   display: flex;
   justify-content: space-between;
   flex-wrap: wrap;
}

.product-galery {
   width: 50%;

   &__wrap {
      position: relative;
      max-width: 485px;
      margin-left: 3%;
   }

}

.product-info {
   width: 50%;
}

.product-order {
   width: 100%;
}

.prod-galery-slider {
   max-width: 485px;
   height: 485px;

   &__item {
      width: 100%;
      height: auto;
      border-radius: 50%;
      overflow: hidden;
      cursor: pointer;
      box-shadow: 0 0 10px rgba(0, 0, 0, .4);

      & img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }
}

.slick-dots li button:before {
   font-size: 16px;
}

.slick-dots li.slick-active button:before {
   color: $white;
}

.slider-btn {
   position: absolute;
   z-index: 99;
   width: 21px;
   height: 34px;
   background: url(../img/icons/icon-arr.png)center center no-repeat;
   opacity: .6;
   transition: all .3s ease-in;

   &:hover {
      opacity: 1;
   }

   &-r {
      top: 0;
      right: 2rem;
   }

   &-l {
      bottom: 0;
      left: 2rem;
      transform: rotate(180deg);
   }
}

.product-info-list {
   max-width: 490px;
   margin-bottom: 1.56rem;
   border-bottom: 1px solid $white;

   &__item {
      display: flex;
      margin-bottom: 1rem;
      color: $white;
      font-size: 1.125rem;

      & span:first-child {
         width: 180px;
      }

      & span:last-child {
         font-weight: 600;
         width: 250px;
      }
   }
}

.prod-sizes {
   max-width: 490px;
   display: flex;
   margin-bottom: 3rem; //====================================

   &__title {
      min-width: 180px;
      color: $white;
      font-size: 1.125rem;
   }
}

.product-info-size {
   display: flex;
   flex-wrap: wrap;

   &__item {
      border-radius: 8px;
      width: 33.33%;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      color: $white;
      font-size: 1.125rem;
      transition: all .3s ease-in;

      & span:first-child {
         font-weight: 600;
      }

      &:hover {
         background-color: #4ecbc6;
      }
   }
}


.active-size {
