.how-wrap{
   max-width: 1080px;
   position: relative;
   &:before{
      content: '';
      position: absolute;
      left: 5%;
      top: 25%;
      width: 90%;
      height: 2px;
      background-image: linear-gradient(to right, #fff 33%, rgba(255,255,255,0) 0%);
